export const required = (value) => {
    return (value ? undefined : ' ')
}
export const maxLength = (max) => (value) => {
    return (value ? (value.length > max ? `  ${max}   ` : undefined) : undefined);
}

export const typeCorrect = (value) => {
    return (value ? (!(value === 'true' || value === 'false') ? `  'true'  'false'` : undefined) : undefined );
}

export const typeCorrectURL = (value) => {
    return (value ? ((value.includes('http://') || value.includes('https://')) ? `    ( http(s)://)` : undefined) : undefined);
}